import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
  ChangeDetectorRef,
  signal,
} from '@angular/core';
import {
  DxListComponent,
  DxListModule,
  DxTooltipModule,
} from 'devextreme-angular';
import { ItemClickEvent } from 'devextreme/ui/list';

// import {MobileListComponent} from '../mobile-list/mobile-list.component';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';

const TOOLTIP_DELAY = 600;

@Component({
  selector: 'app-custom-chips-button',
  templateUrl: './custom-chips-button.component.html',
  styleUrls: ['./custom-chips-button.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: true,
  imports: [
    // MobileListComponent,
    DxListModule,
    DxTooltipModule,
    CommonModule,
    TranslateModule,
  ],
})
export class CustomChipsButtonComponent implements OnInit {
  @ViewChild('listRef') listRef: DxListComponent | any;
  @Input() name: string | null = null;
  @Input() list: { value: any; label: string }[] | any[] = [];
  @Input() width: number | string | null = null;
  @Input() maxHeight: number | string | null = null;
  @Input() customDisplayValue: string | null = null;
  @Input() selectedValueInput: any;
  @Input() disabledClear = false;
  @Input() positionTop: string = '-2px';
  @Input() labelExpr: string = 'label';
  @Input() valueExpr: string = 'value';
  @Input() deleteFilter = false;
  @Input() appDateRangeChips = false;
  @Output() onValueChanged: EventEmitter<any> = new EventEmitter<any>(); // zależy co jest zdefiniowane w list.value
  @Input() deviceType: 'desktop' | 'mobile' = 'desktop';

  unicalGuid = new Date().getTime() + Math.round(Math.random() * 10000);
  // isChipsList: boolean = false;
  isChipsList = signal(false);
  selectedValue: string = '';
  listHeight: number | string | null = null;

  tooltipShowEvent = { name: 'dxhoverstart', delay: TOOLTIP_DELAY };

  @HostListener('document:click')
  clickout() {
    if (!this.eRef.nativeElement.contains(event?.target)) {
      this.isChipsList.set(false);
    }
  }

  constructor(
    private eRef: ElementRef,

    public cd: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.checkSelectedValue();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (
      changes['selectedValueInput']?.previousValue &&
      !changes['selectedValueInput']?.currentValue
    ) {
      this.selectedValue = '';
    }

    if (changes['deleteFilter']?.currentValue) {
      this.clearValue();
    }

    this.checkSelectedValue();

    if (changes['list']?.currentValue || changes['maxHeight']?.currentValue) {
      if (this.list.length && this.maxHeight) {
        const maxHeight = parseInt(this.maxHeight.toString());
        this.listHeight =
          this.list.length * 30 > maxHeight ? maxHeight : 'auto';
      }
    }
  }

  checkSelectedValue() {
    if (this.selectedValueInput) {
      let val = this.list.find(
        (filed) => filed[this.labelExpr] == this.selectedValueInput
      );
      if (val) {
        this.selectedValue = val[this.labelExpr];
      } else if (!val && this.customDisplayValue) {
        const found = this.list.find(
          (filed) => filed[this.valueExpr] == 'custom'
        );
        if (found) {
          this.selectedValue = found[this.labelExpr];
        }
      } else if (!val && this.selectedValueInput === 'Niezapisany filtr') {
        this.selectedValue = 'Niezapisany filtr';
      }
    }
  }

  clearValue = () => {
    this.selectedValue = '';
    this.onValueChanged.emit('');
    this.isChipsList.set(false);
  };

  onItemClick = (e: ItemClickEvent) => {
    if (e.itemData.value === null) {
      this.clearValue();
      return;
    }

    this.selectedValue = e.itemData[this.labelExpr];
    this.cd.detectChanges();

    this.onValueChanged.emit(e.itemData[this.valueExpr]);
    this.isChipsList.set(false);
  };

  swapChipsList() {
    this.isChipsList.set(!this.isChipsList());
    setTimeout(() => {}, 100);
  }
}
