import { CommonModule, DatePipe } from '@angular/common';
import {
  Component,
  ChangeDetectionStrategy,
  ViewEncapsulation,
  forwardRef,
  Output,
  EventEmitter,
  ViewChild,
  ChangeDetectorRef,
} from '@angular/core';
import {
  DxButtonModule,
  DxDateBoxComponent,
  DxDateBoxModule,
  DxTooltipModule,
} from 'devextreme-angular';
import { TranslateModule } from '@ngx-translate/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { ValueChangedEvent } from 'devextreme/viz/range_selector';
import { KeyDownEvent } from 'devextreme/ui/data_grid';

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => WaproDateBoxComponent),
  multi: true,
};

const TOOLTIP_DELAY = 600;

@Component({
  selector: 'wapro-date-box',
  templateUrl: './wapro-date-box.component.html',
  styleUrl: './wapro-date-box.component.css',
  inputs: [
    'displayFormat',
    'readOnly',
    'type',
    'disabled',
    'errorClass',
    'width',
    'noMaxWidth',
    'BLWarning',
    'BLError',
    'opened',
  ],
  standalone: true,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR],
  imports: [
    CommonModule,
    DxDateBoxModule,
    DxTooltipModule,
    TranslateModule,
    DxButtonModule,
  ],
})
export class WaproDateBoxComponent {
  @Output() onValueChanged = new EventEmitter();
  @ViewChild('dateBox') dateBox: DxDateBoxComponent | any;

  @Output() focusedOut = new EventEmitter();
  @Output() focusedIn = new EventEmitter();

  noMaxWidth: boolean = false;
  width: number | string | null = null;
  private _value: string | number | Date = '';
  displayFormat: string = '';
  readOnly = false;
  type: 'date' | 'datetime' | 'time' = 'date';
  disabled = false;
  errorClass = false;
  BLError = false;
  BLWarning = false;

  isHover: boolean = false;
  isFocus: boolean = false;
  plusBtn = {
    icon: 'icon absui-icon--add',
    stylingMode: 'text',
    onClick: () => {
      this.addDay();
    },
  };
  minusBtn = {
    icon: 'icon absui-icon--remove',
    stylingMode: 'text',
    onClick: () => {
      this.subtractDay();
    },
  };
  opened: boolean = false;
  notOpen: boolean = false;
  calendarBtn = {
    icon: 'icon absui-icon--calendar-month-unselect',
    elementAttr: { 'data-cy': 'calendar-btn-open' },
    stylingMode: 'text',
    tabIndex: -1,
    onClick: () => {
      this.opened = !this.opened;
      this.cd.detectChanges();
    },
  };
  calendarOptions: any;
  changeByUser: boolean = false;

  public get myValue(): string | number | Date {
    return this._value;
  }
  public set myValue(v: string | number | Date) {
    if (v !== this._value) {
      this._value = v;
      try {
        this.onChange(v);
      } catch {}
    }
  }

  tooltipShowEvent = { name: 'dxhoverstart', delay: TOOLTIP_DELAY };

  constructor(public cd: ChangeDetectorRef) {
    if (!this.displayFormat) this.displayFormat = 'yyyy-MM-dd';
    if (!this.type) this.type = 'date';
    if (!this.disabled) this.disabled = false;

    this.calendarOptions = {
      onContentReady: (e: any) => {
        setTimeout(function () {
          e.component.option('inputAttr', { readonly: true });
        }, 500);
        let todayBtn =
          this.dateBox.element.nativeElement.getElementsByClassName(
            'dx-button-today'
          )[0] as HTMLElement;
        if (this.type != 'datetime') {
          let buttonDone = document.getElementsByClassName(
            'dx-widget dx-button dx-button-mode-contained dx-button-normal dx-button-has-text dx-popup-done'
          );
          let buttonCancel = document.getElementsByClassName(
            'dx-widget dx-button dx-button-mode-contained dx-button-normal dx-button-has-text dx-popup-cancel'
          );

          for (let i = 0; i < buttonDone.length; i++) {
            buttonDone[i].classList.add('disabledBtn');
          }

          for (let i = 0; i < buttonCancel.length; i++) {
            buttonCancel[i].classList.add('disabledBtn');
          }

          e.element.onclick = () => {
            let newDate = new DatePipe('en-US').transform(
              e.component.option('value'),
              'yyyy-MM-ddTHH:mm:ss'
            );
            this.changeByUser = true;
            this.myValue = newDate as string;
            this.cd.detectChanges();
          };
        }

        todayBtn.onclick = () => {
          let newDate = new DatePipe('en-US').transform(
            new Date(),
            'yyyy-MM-ddTHH:mm:ss'
          );
          this.changeByUser = true;
          this.myValue = newDate as string;
          this.opened = false;
          this.cd.detectChanges();
        };
      },
    };
  }

  addDay = () => {
    if (
      this.myValue == null ||
      this.myValue == undefined ||
      this.myValue == ''
    ) {
      this.myValue = new DatePipe('en-US').transform(
        new Date(),
        'yyyy-MM-ddTHH:mm:ss'
      ) as string;
    }

    let newDate = new DatePipe('en-US').transform(
      new Date(this.myValue).getTime() + 1 * 1000 * 60 * 60 * 24,
      'yyyy-MM-ddTHH:mm:ss'
    );
    this.changeByUser = true;
    this.myValue = newDate as string;
    this.cd.detectChanges();
  };

  openBox = () => {
    if (!this.notOpen) {
      this.opened = !this.opened;
    }
  };

  onOpened = () => {
    this.notOpen = true;
  };

  onClosed = () => {
    setTimeout(() => {
      this.notOpen = false;
    }, 500);
  };

  onValueChangedDate = (e: any) => {
    if (this.type != 'datetime' && !this.areDatesEqual(e)) {
      this.opened = false;
      this.cd.detectChanges();
    }
    const event = { ...e };
    if (!event.event) {
      event.event = {} as any;
    }
    this.onValueChanged.emit(event);
    this.changeByUser = false;
  };

  areDatesEqual(e: any) {
    if (!e.value || (e.value && !e.previousValue)) {
      return false;
    }
    try {
      const currentValue = new DatePipe('en-US').transform(
        new Date(e.value),
        this.displayFormat
      );
      const previousFormattedValue = new DatePipe('en-US').transform(
        new Date(e.previousValue),
        this.displayFormat
      );

      return currentValue === previousFormattedValue;
    } catch {
      return false;
    }
  }

  onBlur = (e: any) => {
    e.element.classList.remove('dx-state-focused');
  };

  subtractDay = () => {
    if (
      this.myValue == null ||
      this.myValue == undefined ||
      this.myValue == ''
    ) {
      this.myValue = new DatePipe('en-US').transform(
        new Date(),
        'yyyy-MM-ddTHH:mm:ss'
      ) as string;
    }

    let newDate = new DatePipe('en-US').transform(
      new Date(this.myValue).getTime() - 1 * 1000 * 60 * 60 * 24,
      'yyyy-MM-ddTHH:mm:ss'
    );
    this.changeByUser = true;
    this.myValue = newDate as string;
    this.cd.detectChanges();
  };

  keydown = (e: any) => {
    if (!this.readOnly) {
      const originalEvent = (e.event as any).originalEvent;
      if (originalEvent.code == 'ArrowDown' && originalEvent.shiftKey)
        this.subtractDay();
      if (originalEvent.code == 'ArrowUp' && originalEvent.shiftKey)
        this.addDay();
      this.cd.detectChanges();
    }
  };

  mouseover = () => {
    this.isHover = true;
  };

  mouseout = () => {
    this.isHover = false;
  };

  onFocusIn = () => {
    this.isFocus = true;
    this.focusedIn.emit(true);
  };

  onFocusOut = () => {
    this.isFocus = false;
    this.focusedOut.emit(true);
  };

  onChange = (_: any) => {};
  onTouched = () => {};

  writeValue(value: any): void {
    this.myValue = value;
    this.cd.detectChanges();
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
    this.cd.detectChanges();
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
    this.cd.detectChanges();
  }
  setDisabledState?(_isDisabled: boolean): void {
    //throw new Error('Method not implemented.');
  }
}
